"use client";

import React, { useState } from "react";
import { Box, Typography, Menu, MenuItem, IconButton } from "@mui/material";
import LanguageIcon from "@mui/icons-material/Language";
import MonetizationOnIcon from "@mui/icons-material/MonetizationOn";

type Props = {
  language: "en" | "ka";
  setLanguage: (lang: "en" | "ka") => void;
  currency: "USD" | "GEL";
  setCurrency: (cur: "USD" | "GEL") => void;
};

const Filter = ({ language, setLanguage, currency, setCurrency }: Props) => {
  const [anchorElLang, setAnchorElLang] = useState<null | HTMLElement>(null);
  const [anchorElCurrency, setAnchorElCurrency] = useState<null | HTMLElement>(null);

  const handleLanguageClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorElLang(event.currentTarget);
  };

  const handleLanguageClose = (value?: "en" | "ka") => {
    if (value) setLanguage(value);
    setAnchorElLang(null);
  };

  const handleCurrencyClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorElCurrency(event.currentTarget);
  };

  const handleCurrencyClose = (value?: "USD" | "GEL") => {
    if (value) setCurrency(value);
    setAnchorElCurrency(null);
  };

  return (
    <Box display="flex" justifyContent="space-between" alignItems="center" sx={{ mb: 2 }}>
      {/* Language Selector */}
      <Box>
        <IconButton onClick={handleLanguageClick} color="primary" size="large">
          <LanguageIcon />
        </IconButton>
        <Menu
          anchorEl={anchorElLang}
          open={Boolean(anchorElLang)}
          onClose={() => handleLanguageClose()}
        >
          <MenuItem
            selected={language === "en"}
            onClick={() => handleLanguageClose("en")}
          >
            English
          </MenuItem>
          <MenuItem
            selected={language === "ka"}
            onClick={() => handleLanguageClose("ka")}
          >
            ·É•·Éê·É†·Éó·É£·Éö·Éò
          </MenuItem>
        </Menu>
      </Box>

      {/* Currency Selector */}
      <Box>
        <IconButton onClick={handleCurrencyClick} color="primary" size="large">
          <MonetizationOnIcon />
        </IconButton>
        <Menu
          anchorEl={anchorElCurrency}
          open={Boolean(anchorElCurrency)}
          onClose={() => handleCurrencyClose()}
        >
          <MenuItem
            selected={currency === "USD"}
            onClick={() => handleCurrencyClose("USD")}
          >
            USD
          </MenuItem>
          <MenuItem
            selected={currency === "GEL"}
            onClick={() => handleCurrencyClose("GEL")}
          >
            GEL
          </MenuItem>
        </Menu>
      </Box>
    </Box>
  );
};

export default Filter;
