"use client";

import React from "react";
import { Grid, Card, CardContent, Typography } from "@mui/material";

type Props = {
  language: "en" | "ka";
  currency: "USD" | "GEL";
};

const conversionRate = 3.0; // Example: 1 USD = 3 GEL

const originalFlowers = [
  {
    id: 1,
    name_en: "Red Rose",
    name_ka: "·É¨·Éò·Éó·Éî·Éö·Éò ·Éï·Éê·É†·Éì·Éò",
    priceUSD: 10,
    description_en: "A beautiful red rose",
    description_ka: "·Éõ·É®·Éï·Éî·Éú·Éò·Éî·É†·Éò ·É¨·Éò·Éó·Éî·Éö·Éò ·Éï·Éê·É†·Éì·Éò",
  },
  {
    id: 2,
    name_en: "White Lily",
    name_ka: "·Éó·Éî·Éó·É†·Éò ·Éö·Éò·Éö·Éî",
    priceUSD: 12,
    description_en: "Fresh white lilies",
    description_ka: "·Éõ·É¨·Éï·Éê·Éú·Éî ·Éó·Éî·Éó·É†·Éò ·Éö·Éò·Éö·Éî·Éî·Éë·Éò",
  },
  // Add more flowers here...
];

const MainContent = ({ language, currency }: Props) => {
  // Convert prices based on currency
  const flowers = originalFlowers.map((flower) => {
    const price =
      currency === "USD"
        ? `$${flower.priceUSD.toFixed(2)}`
        : `${(flower.priceUSD * conversionRate).toFixed(2)}‚Çæ`;

    return {
      ...flower,
      price,
    };
  });

  return (
    <Grid container spacing={3}>
      {flowers.map((flower) => (
        <Grid item xs={12} md={4} key={flower.id}>
          <Card>
            <CardContent>
              <Typography variant="h6">
                {language === "en" ? flower.name_en : flower.name_ka}
              </Typography>
              <Typography color="text.secondary">{flower.price}</Typography>
              <Typography variant="body2" sx={{ mt: 1 }}>
                {language === "en" ? flower.description_en : flower.description_ka}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      ))}
    </Grid>
  );
};

export default MainContent;
